<script>
import headers from "@/components/header"
import logint from "@/components/logintan"
export default {
  components:{
    headers,
    logint
  },
  data() {
   return {
     tableData3: [{
          date: '2016-05-03',
          name: '张土豪',
          address: '上海市普陀区金沙江路 1518 弄',
          shuzi1: '3435343'
        }, {
          date: '2016-05-02',
          name: '社会网',
          address: '上海市普陀区金沙江路 1518 弄',
          shuzi1: '7628595'
        }],
        multipleSelection: [],
     input: '',
     input1: '',
     input2: '',
     input3: '',
     input4: '',
     input5: '',
     input6: '',
      dialogVisible: false,
      value1: new Date(2016, 9, 10, 18, 40),
      value2: new Date(2016, 9, 10, 18, 40),
      options: [{
          value: '选项1',
          label: '已还款'
        }, {
          value: '选项2',
          label: '未还款'
        }],
        value: ''
   }
 },
 methods: {
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      open3() {
        this.$prompt('请输入密码', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: '密码正确'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
      }
    }

}
