<template lang="html">
  <div class="header-wrapper">
    <img src="../assets/logo.png" alt="" />
    <ul>
      <router-link tag="li" active-class="act" :to="{path:'/index'}">首页</router-link>
      <router-link tag="li" active-class="act" :to="{path:'/motorcade'}">我的车队</router-link>
      <router-link tag="li" active-class="act" :to="{path:'/waybill'}">运单管理</router-link>
      <router-link tag="li" active-class="act" :to="{path:'/payMoney'}">付款记录</router-link>
      <router-link tag="li" active-class="act" :to="{path:'/debitNode'}">发票管理</router-link>
      <router-link tag="li" active-class="act" :to="{path:'/factoring'}">保理业务</router-link>
      <router-link tag="li" active-class="act" :to="{path:'/freight'}">运费账户</router-link>
    </ul>
    <div class="dz">
      <a href="javascript:;" @click="close()">登录</a>
      <router-link tag="a" :to="{path:'/indexZc'}">注册</router-link>
    </div>
  </div>
</template>

<script>
export default {
  methods:{
    close(){
      $('.login').fadeIn(500)
    }
  }
}
</script>

<style lang="less">
  .header-wrapper{
    width: 1200px;
    background: white;
    margin: 0 auto;
    &:after{
      content: '';
      display: block;
      clear: both;
    }
    img{
      float: left;
      padding: 7px 0;
    }
    ul{
      float: left;
      &:after{
        content: '';
        display: block;
        clear: both;
      }
      li{
        float: left;
        font-size: 18px;
        color: #343434;
        margin: 0 25px;
        line-height: 80px;
        &.act{
          color: #2e51a5;
          font-weight: bolder;
          border-bottom: 2px solid #2e51a5;
        }
      }
    }
    .dz{
      float: right;
      a{
        font-size: 14px;
        color: #9c9c9c;
        padding: 0 10px;
        line-height: 80px;
      }
    }
  }
</style>



