<template>
	<div class="logintan">
		<div class="login_contan">
			<img src="../assets/logo.png" alt=""><br />
			<input type="text" name="" placeholder="请输入账号">
			<input type="password" name="" placeholder="*******">
			<button @click="close()">登录</button>
			<p>
				<router-link class="forget" tag="a" :to="{path:'/indexz'}">忘记密码？</router-link>
				<router-link class="freezc" tag="a" :to="{path:'/indexZc'}">免费注册</router-link>
			</p>
			<span class="login_close" @click="close()">×</span>
		</div>
	</div>
</template>
<script>
	export default{
		methods:{
			close(){
				// var logintan=document.getElementsByClassName('logintan')[0];
				// logintan.style.display="none";
				$('.logintan').fadeOut(500)
			}
		}
	}
</script>
<style>

.logintan{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background: rgba(0, 0, 0, 0.5)
}
.login_contan{
	width: 500px;
	height: 420px;
	position: absolute;
	margin: auto;
	left: 50%;
	top: 50%;
	text-align: center;
	margin: -210px -250px;
	background: #fff;
	box-sizing: border-box;
	padding: 40px 110px;
	border-radius: 3px;
}
.login_contan img{
	width: 170px;
	height: 90px;
}
.login_contan input{
	width: 100%;
	height: 40px;
	border:  2px solid rgb(240, 240, 240);
	border-radius: 2px;
	text-align: center;
	background: rgb(245, 246, 251);
	margin-top: 20px;
	outline: none;
}
.login_contan button{
	width: 100%;
	margin-top: 30px;
	height: 40px;
	color: #fff;
	background: rgb(0, 82, 255);
	border: 0;
	border-radius: 2px;
}
.login_contan p{
	margin-top: 10px;
}
.login_contan p a{
	font-size: 14px;
	text-decoration: none;
}
.login_contan p .forget{
	color: rgb(200, 200, 200);
	float: left;
}
.login_contan p .freezc{
	float: right;
	color: rgb(210, 20, 20);
}
.login_contan span{
	position: absolute;
	right: 0;
	top: 0;
	padding: 10px 20px;
	font-size: 30px;
	cursor: pointer;
	color: rgb(156, 156, 156);
}
</style>



